.income-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.income-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2c2c2c;
    border-radius: 10px;
    padding: 0.8rem 1rem;
    margin-bottom: 0.8rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.income-item__description {
    display: flex;
    flex-direction: column;
    color: white;
}

.income-item__amount {
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    background-color: #4caf50;
    padding: 0.4rem 0.8rem;
    border-radius: 6px;
}

.income-item__date {
    font-size: 0.9rem;
    color: #aaa;
}