.diagram{
    padding: 1rem;
    border-radius: 12px;
          
    text-align: center;
    display: flex;
    justify-content: space-between;
    height: 10rem;
    
    background-color: #282c34;
        /* Darker gray diagram background */
        color: #eee;
}

.diagram-bar-container {
    position: relative;
    /* Для абсолютного позиционирования суммы */
    margin-bottom: 10px;
}

.bar-cost {
    position: absolute;
    top: -20px;
    /* Позиционирование над столбиком */
    left: 50%;
    transform: translateX(-50%);
    font-weight: bold;
    font-size: 12px;
}

@media (max-width: 400px) {
    .bar-cost {
        font-size: 8px;
        top: -15px;
    }
}


 

.diagram-toggle {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    gap: 0.5rem;
}

.toggle-switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 20px;
}

.toggle-switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
    border-radius: 20px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 14px;
    width: 14px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}

input:checked+.slider {
    background-color: #007bff;
}

input:checked+.slider:before {
    transform: translateX(20px);
}

.toggle-label {
    color: #eee;
    font-size: 0.9rem;
}