/* PieChartComponent.css */

/* Основной контейнер для графика */
.pie-chart-container {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    margin-bottom: 20px;
    overflow: hidden;
    /* Добавлено для предотвращения выхода содержимого за пределы контейнера */
}

/* Стили для адаптивности */
@media (max-width: 600px) {
    .pie-chart-container {
        max-width: 100%;
        /* Занимает всю ширину экрана на мобильных устройствах */
    }

    .recharts-legend-wrapper {
        font-size: 10px;
        /* Уменьшаем размер шрифта для мобильных */
        white-space: nowrap;
        /* Предотвращает перенос текста */
        overflow: hidden;
        /* Скрывает текст, который не помещается */
        text-overflow: ellipsis;
        /* Добавляет многоточие, если текст не помещается */
    }

    .recharts-legend-item {
        flex: 1 1 50%;
        /* Уменьшаем размер элементов легенды, делая их более компактными */
        max-width: 100%;
        /* Ограничивает максимальную ширину элемента */
    }

    .recharts-legend-item-text {
        white-space: nowrap;
        /* Предотвращает перенос текста */
        overflow: hidden;
        /* Скрывает текст, который не помещается */
        text-overflow: ellipsis;
        /* Добавляет многоточие, если текст не помещается */
    }
}