.cost-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2c2c2c;
    border-radius: 10px;
    padding: 0.8rem 1rem;
    margin-bottom: 0.8rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    
}
  

.cost-item__left {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.cost-item__description {
    display: flex;
    flex-direction: column;
}

.cost-item__description h3 {
    font-size: 1rem;
    color: #fff;
    margin: 0;
}

.cost-item__category {
    font-size: 0.85rem;
    color: #aaa;
    margin-top: 0.2rem;
}

.cost-item__right {
    display: flex;
    align-items: center;
    gap: 0.8rem;
}

.cost-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    background-color: #9eaf4c;
    padding: 0.4rem 0.8rem;
    border-radius: 6px;
}

.cost-item__edit,
.cost-item__delete {
    background-color: transparent;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    transition: color 0.2s ease;
}

.cost-item__edit {
    color: #4caf50;
}

.cost-item__edit:hover {
    color: #35a235;
}

.cost-item__delete {
    color: #ff6b6b;
}

.cost-item__delete:hover {
    color: #ff3b3b;
}

/* Убираем маркеры списков */
li {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

ul {
    padding: 0;
    margin: 0;
}


.cost-item__right {
    display: flex;
    align-items: center;
    /* Выравнивание по вертикали */
    gap: 0.8rem;
}

.cost-item__edit,
.cost-item__delete {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.5rem;
    /* Фиксированная ширина для обеих кнопок */
    height: 2.5rem;
    /* Фиксированная высота для обеих кнопок */
    background-color: transparent;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    transition: color 0.2s ease;
}

.cost-item__edit {
    color: #4caf50;
}

.cost-item__edit:hover {
    color: #35a235;
}

.cost-item__delete {
    color: #ff6b6b;
}

.cost-item__delete:hover {
    color: #ff3b3b;
}

@media (max-width: 768px) {
    .cost-item {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.5rem;
        width: 100%;
        margin-left: 0;
        padding: 0.8rem;
        box-sizing: border-box;
    }

    .cost-item__left {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        width: 100%;
    }

    .cost-item__description {
        font-size: 0.9rem;
        flex-grow: 1;
    }

    .cost-item__price {
        font-size: 0.9rem;
        padding: 0.4rem 0.6rem;
        text-align: center;
        flex-shrink: 0;
        min-width: 70px;
        border-radius: 4px;
    }

    .cost-item__right {
        display: flex;
        justify-content: flex-end;
        gap: 0.5rem;
        width: 100%;
    }

    .cost-item__edit,
    .cost-item__delete {
        font-size: 1.2rem;
        width: 2rem;
        height: 2rem;
    }
}