/* Основной контейнер шапки */
.app-header {
    background-color: #1e1e1e;
    color: white;
    padding: 10px 20px;
}

.header-content {
    display: flex;
    justify-content: space-between;
    /* Элементы равномерно распределяются: слева и справа */
    align-items: center;
    /* Вертикальное выравнивание */
}

/* Левая часть шапки */
.header-left h1 {
    font-size: 1.8rem;
    margin: 0;
}

.header-date {
    font-size: 1rem;
    color: #ccc;
    margin-top: 5px;
}

/* Правая часть шапки */
.header-right {
    display: flex;
    align-items: center;
}

/* Аватар пользователя */
.user-avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #4a90e2;
    color: white;
    font-size: 1.5rem;
    font-weight: bold;
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.user-avatar:hover {
    transform: scale(1.1);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

/* Модальное окно */
.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal {
    background: #1e1e1e;
    color: white;
    padding: 20px;
    border-radius: 8px;
    width: 300px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
     
}

.modal-content {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.primary-button {
    background-color: #4a90e2;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.primary-button:hover {
    background-color: #357ac9;
}

.logout-button,
.close-button {
    background-color: #e04b59;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
.logout-button{
background-color: #4a90e2;

}

.logout-button:hover,
.close-button:hover {
    background-color: #c03a47;
}
.logout-button:hover{
    background-color: #357ac9;
}

body.modal-open {
    overflow: hidden;
    position: fixed;
    width: 100%;
}

.modal-content input[type="password"] {
    width: 100%;
    /* Поля занимают всю ширину контейнера */
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #2e2e2e;
    color: white;
    box-sizing: border-box;
    /* Учитываем padding в общей ширине */
}

.modal-content input[type="password"]:focus {
    outline: none;
    border-color: #4a90e2;
    background-color: #3a3a3a;
}

/* Название приложения */
.app-title {
    font-family: 'Poppins', sans-serif;
    font-size: 2rem;
    font-weight: 600;
    color: #4a90e2;
    position: relative;
    display: inline-block;
    text-transform: capitalize;
    margin: 0;
}

.app-title span {
    color: #e04b59;
    font-weight: 700;
}

