/* Дата под заголовком */
.header-date {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    color: #ccc;
    margin-top: 5px;
}


.language-selector {
  text-align: center;
  margin: 20px 0;
}

.language-selector h4 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
  color: #333;
}

.language-button {
  background: linear-gradient(135deg, #6a11cb, #2575fc); /* Градиентный фон */
  color: white; /* Белый текст */
  border: none; /* Убираем рамку */
  border-radius: 20px; /* Закруглённые углы */
  padding: 10px 20px; /* Внутренние отступы */
  font-size: 14px; /* Размер текста */
  margin: 5px; /* Расстояние между кнопками */
  cursor: pointer; /* Указатель при наведении */
  transition: all 0.3s ease; /* Плавный переход для эффектов */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Лёгкая тень */
}

.language-button:hover {
  transform: scale(1.1); /* Увеличение при наведении */
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2); /* Увеличенная тень */
}

.language-button.active {
  background: linear-gradient(135deg, #34d399, #059669); /* Цвет для активной кнопки */
  box-shadow: 0 4px 10px rgba(52, 211, 153, 0.5); /* Более заметная тень */
  transform: scale(1.05); /* Лёгкое увеличение активной кнопки */
}

.language-button:focus {
  outline: none; /* Убираем стандартную обводку */
  box-shadow: 0 0 0 4px rgba(58, 122, 248, 0.5); /* Добавляем обводку при фокусе */
}
.language-selector {
    display: flex;
    flex-direction: column;
    /* Выстраиваем текст и кнопки по вертикали */
    align-items: center;
    /* Центрируем текст и кнопки */
    gap: 10px;
    /* Отступ между текстом и кнопками */
    margin: 10px 0;
    /* Общие отступы для блока */
}

.language-selector h4 {
    font-size: 14px;
    /* Компактный размер текста */
    font-weight: 500;
    /* Полужирный текст */
    color: #555;
    /* Нейтральный серый цвет */
    margin: 0;
    /* Убираем отступы заголовка */
}

.language-button {
    background-color: #f4f4f4;
    /* Светло-серый фон */
    color: #333;
    /* Тёмный текст */
    border: 1px solid #ddd;
    /* Лёгкая граница */
    border-radius: 5px;
    /* Лёгкое закругление углов */
    padding: 6px 12px;
    /* Компактные отступы внутри кнопок */
    font-size: 14px;
    /* Размер текста */
    cursor: pointer;
    /* Указатель при наведении */
    transition: all 0.2s ease;
    /* Плавный переход для эффектов */
}

.language-button:hover {
    background-color: #eaeaea;
    /* Более тёмный фон при наведении */
    border-color: #ccc;
    /* Подчёркнутая граница */
}

.language-button.active {
    background-color: #007bff;
    /* Активный синий фон */
    color: white;
    /* Белый текст */
    border-color: #0056b3;
    /* Тёмно-синий контур */
}

.language-button:focus {
    outline: none;
    /* Убираем стандартную обводку */
    box-shadow: 0 0 2px 2px rgba(0, 123, 255, 0.5);
    /* Лёгкая подсветка при фокусе */
}

.language-button:active {
    background-color: #0056b3;
    /* Более тёмный фон при нажатии */
    border-color: #004085;
}