.auth-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    /* Изменено с height на min-height */
    background-color: #282c34;
    padding: 20px;
    /* Добавлен padding для более аккуратного вида */
    box-sizing: border-box;
}

.auth-form {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 380px;
}

.auth-input {
    width: 100%;
    /* Сделано 100% для лучшей адаптивности */
    padding: 12px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    box-sizing: border-box;
}

.auth-input:focus {
    border-color: #4CAF50;
    outline: none;
}

.auth-buttons {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.auth-button {
    padding: 12px;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-bottom: 10px;
}

.auth-button.signup {
    background-color: #4CAF50;
    color: white;
}

.auth-button.signup:hover {
    background-color: #45a049;
}

.auth-button.signin {
    background-color: #2196F3;
    color: white;
}

.auth-button.signin:hover {
    background-color: #0b7dda;
}

.error-message {
    color: red;
    font-size: 14px;
    margin-bottom: 10px;
}

@media (max-width: 600px) {
    .auth-form {
        width: 100%;
        max-width: 90%;
    }

    .auth-heading {
        font-size: 1.5rem;
    }

    .auth-input {
        font-size: 0.9rem;
        padding: 10px;
    }

    .auth-button {
        font-size: 0.9rem;
        margin-bottom: 10px;
    }
}
.language-selector {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    /* Расстояние между кнопками */
    margin-bottom: 20px;
   
    /* Позволит переносить кнопки, если экран узкий */
    width: 100%;
    max-width: 380px;
    /* Совпадает с шириной формы */
}

.language-button {
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    color: #fff;
    background: #444;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
    min-width: 110px;
    /* Фиксированная ширина кнопок */
    text-align: center;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.language-button.active {
    background: #4CAF50;
    box-shadow: 0px 4px 10px rgba(76, 175, 80, 0.6);
}

.language-button:hover {
    background: #555;
    transform: scale(1.05);
}

/* Для мобильных */
@media (max-width: 600px) {
    .language-selector {
        gap: 10px;
        max-width: 100%;
    }

    .language-button {
        font-size: 12px;
        padding: 8px 15px;
        min-width: 90px;
    }
}