.cost-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2c2c2c;
    border-radius: 10px;
    padding: 0.8rem 1rem;
    margin-bottom: 0.8rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.cost-item__left {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.cost-item__description h3 {
    font-size: 1rem;
    color: #fff;
}

.cost-income-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    background-color: #4caf50;
    padding: 0.4rem 0.8rem;
    border-radius: 6px;
}

.cost-income-item__type {
    font-size: 0.9rem;
    font-weight: bold;
    color: #fff;
    background-color: #00ff4c3c;
    /* Оранжевый фон для выделения */
    padding: 0.2rem 0.5rem;
    border-radius: 4px;
    margin-left: 1rem;
}

.cost-item__type {
    font-size: 0.9rem;
    font-weight: bold;
    color: #fff;
    background-color: #007bff39;
    /* Оранжевый фон для выделения */
    padding: 0.2rem 0.5rem;
    border-radius: 4px;
    margin-left: 1rem;
}