.exchange-rates {
    
    padding: 10px;
    border-radius: 10px;
    width: fit-content;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    font-size: 14px;
    color: rgb(251, 246, 246);
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 20px;
    /* Отступы между курсами */
}

.exchange-rates h3 {
    margin: 0;
    font-size: 16px;
}

.exchange-rates span {
    font-weight: bold;
}