body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  /* Modern font */
  background-color: #1e1e1e;
  /* Dark gray background */
  color: #eee;
  /* Light gray text */
  line-height: 1.6;
    min-height: 200vh !important;
  margin-left: 2%;
  margin-right: 2%;
}
 

 .savings-summary .currency {
   font-size: 1.2rem;
   color: #b6b6b6;
   font-weight: 400;
 }

 /* Стиль для кнопок */
 .savings-summary button {
   background-color: #ffbb00;
   padding: 10px 20px;
   border: none;
   border-radius: 8px;
   color: #fff;
   font-size: 1.1rem;
   cursor: pointer;
   margin-top: 20px;
   transition: background-color 0.3s ease;
 }
 

 
 /* Стили для блока с сбережениями */
 .savings-container {
   background: linear-gradient(135deg, #6a11cb 0%, #2575fc 100%);
   /* Градиент для фона */
   color: #fff;
   padding: 20px;
   border-radius: 12px;
   /* Скругленные углы */
   box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
   /* Легкая тень */
   margin-top: 20px;
   display: flex;
   justify-content: center;
   align-items: center;
   font-size: 24px;
   font-weight: bold;
   transition: transform 0.3s ease, box-shadow 0.3s ease;
   /* Анимация при наведении */
 }

 .savings-container:hover {
   transform: scale(1.05);
   /* Увеличение размера при наведении */
   box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
   /* Увеличение тени */
 }

 .savings-container h2 {
   margin: 0;
   font-size: 28px;
   text-align: center;
 }