.edit-cost-form {
    
    max-width: 100%;
    margin: 0 auto;
    background-color: #222;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-left: 2%;
    margin-right: 2%;
}

.edit-cost-form__controls {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.edit-cost-form__control {
    display: flex;
    flex-direction: column;
}

.edit-cost-form__control label {
    margin-bottom: 5px;
    color: #fff;
    font-weight: bold;
}

.edit-cost-form__control input,
.edit-cost-form__control select {
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ccc;
    background-color: #333;
    color: #fff;
    width: 80%;
    
    /* Выравниваем ширину всех полей */
}
.edit-cost-form__control input{
    margin-bottom: 10px;
}

.edit-cost-form__control select {
    margin-bottom: 20px;
    /* Добавлен отступ снизу */
    appearance: none;
        border: 1px solid #585757;
        color: #b6b6b6;
        background-color: #343237;
        
        
    /* Убираем стандартные стили браузера */
}

.edit-cost-form__actions {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.edit-cost-form__actions button {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.edit-cost-form__actions button[type="submit"] {
    background-color: #007bff;
    color: #fff;
}

.edit-cost-form__actions button[type="button"] {
    background-color: #6c757d;
    color: #fff;
    
}

.edit-cost-form__actions button:hover {
    opacity: 0.9;
}

/* Адаптация для мобильных устройств */
@media (max-width: 768px) {
    .edit-cost-form {
        padding: 15px;
    }

    .edit-cost-form__control select {
         
        width: 86%;
        /* В мобильной версии занимает всю ширину */
    }
}
 

.edit-cost-form__control {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.edit-cost-form__control label {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    /* Расстояние между чекбоксом и текстом */
    margin-bottom: 5px;
    color: #fff;
    font-weight: bold;
}

.edit-cost-form__control input[type="checkbox"] {
    width: auto;
    /* Убираем ширину для чекбокса */
    margin: 0;
    visibility: none;
    /* Убираем отступы */
}